
let URL = "http://localhost:8081";
export default {
  name: "CardComponent",
  props: {
    fetchCards: Function,
    cardDone: Function,
    containerID:{},
    cardData: Object,
    cardIndex: Number,
    updateCard:Function,
    
  },
  data: function () {
    return {
      editing: false,
      newCard: {},
    }
  },
  created() {
    // console.log(this.$props.cardData);
  },
  methods: {
    //
    deleteCard:async function(){
      console.log(this.containerID);
      let response = await fetch(`${URL}/container/${this.containerID}/card/${this.cardData._id}`,{
        method:"DELETE",
        credentials: "include",
      });

      if(response.status == 200){
        console.log("delete card successful");
        this.fetchCards();
      } else{
        console.log("Error while deleting",response.status);
      }
    },

    editCard: function () {
      if (!this.editing) {
        this.newCard = {...this.cardData};
        this.editing = true;
      } else {
        this.editing = false;
      }
    },
    updateCardLocal: async function () {
      await this.updateCard(this.cardIndex, this.newCard);
      this.editing = false;
    },
    cancelUpdate: function () {
      this.editing = false;
    },
    
  },
};
