
import {URL} from './config';
import Vue from "vue";

export default Vue.extend({
  name: "App",

  components: {},

  data: () => ({
    drawer: false,
    userCard: true,
    username: "",
    items: [
      {
        title: "My Board",
        link: "/board",
        icon: "mdi-hammer",
      },
      {
        title: "About us",
        link: "/about",
        icon: "mdi-account-group",
      },
      {
        title: "Contact us",
        link: "/contact",
        icon: "mdi-card-account-mail",
      },
    ],
  }),
  methods: {
    changeRoute(route){
      this.$router.replace({path:route});
    },
    // will get the name so that it can show up at the top
    kalebsMethod: async function () {
      let response = await fetch(URL + "/session", {
        method: "GET",
        credentials: "include",
      });
      if (response.status != 200 && this.$router.currentRoute.path != '/login') {
        this.$router.replace({path: "/login"});
      } else if (response.status == 200) {
        let body = await response.json();
        this.username = body.name;
        if (this.$router.currentRoute.path != '/board')
          this.$router.replace({path: "/board"});
      }
    },
    openNav: function () {
      if (this.username) {
        this.drawer = !this.drawer;
      }
    }
  },
  created() {
    this.kalebsMethod();
  },
});
